import { Button, Card, Col, Form, Image, ListGroup, Row } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux"
import { Link, useNavigate, redirect } from "react-router-dom";
import Message from "../components/Message";
import { CART_ADD_ITEM, CART_REMOVE_ITEM } from "../redux/cartSlice"


const CartScreen = () => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const { cartItems } = useSelector(state => state.cart)
    const removeFromCartHandler = (id) => {
        dispatch(CART_REMOVE_ITEM(id))
    }

    const checkOutHandler = () => {
        navigate("/login?redirect=shipping")
    }

    // const subtotal = cartItems.reduce(
    //     (accumulator, currentValue) => accumulator + Number(currentValue.qty),
    //     0
    // )

    return (
        <Row>
            <Col md={8}>
                <h1>Shopping Cart</h1>
                {cartItems.length === 0 ? <Message>Your cart is empty <Link to="/">Go Back</Link></Message> : (
                    <ListGroup variant="flush">
                        {cartItems.map(item => (
                            <ListGroup.Item key={item._id}>
                                <Row>
                                    <Col md={2}>
                                        <Image src={item.image} alt={item.name} fluid rounded />
                                    </Col>
                                    <Col md={3}><Link to={`/produt/${item._id}`}>{item.name}</Link>
                                    </Col>
                                    <Col md={2}>${item.price}</Col>
                                    <Col md={2}>
                                        <Form.Control as="select" defaultValue={item.qty} onChange={e => dispatch(CART_ADD_ITEM(
                                            {
                                                ...item,
                                                qty: (e.target.value)
                                            }))}>
                                            {[...Array(item.countInStock).keys()].map(x => (
                                                <option key={x + 1} value={x + 1}>{x + 1}</option>
                                            ))}
                                        </Form.Control>
                                    </Col>
                                    <Col md={2}>
                                        <Button type="button" variant="light" onClick={() => removeFromCartHandler(item._id)}>
                                            <i className="fas fa-trash"></i>
                                        </Button></Col>
                                </Row>
                            </ListGroup.Item>
                        ))}
                    </ListGroup>
                )}
            </Col>
            <Col md={4}>
                <Card>
                    <ListGroup variant="flush">
                        <ListGroup.Item>
                            <h2>
                                Subtotal ({cartItems.reduce((acc, item) => acc + Number(item.qty), 0)}) items
                            </h2>
                            $({cartItems.reduce((acc, item) => acc + (item.qty) * Number(item.price), 0).toFixed(2)})
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Button type="button" className="btn-block" disabled={cartItems.length === 0} onClick={checkOutHandler}>
                                Proceed to Checkout
                            </Button>
                        </ListGroup.Item>
                    </ListGroup>
                </Card>
            </Col>

        </Row >
    );
}

export default CartScreen;